.casedetail {
  background-color: white;
  padding: 8px 16px 16px 16px;
  margin-top: 16px;
}

.cycledetails {
  background-color: white;
  padding: 16px 16px 16px 16px;
  margin-top: 16px;
}

.appointment {
  color: var(--primary-color);
  font-size: 24px;
  font-weight: bold;
  vertical-align: top;
}
.spacebetween {
  display: flex;
  justify-content: space-between;
}

.table-container-cyclebycase {
  margin: 0 16px 32px;
  background-color: var(--grey-color);
  padding: 16px;
  border-radius: 20px;
  min-height: calc(100vh - 150px);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  position: relative;
}

.recheckmedicinebutton2{
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 32px;
  width: calc(100% - 40px);
}
